import React from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Cell } from 'recharts';

// 
// NIRA
// 
const industryData = [
  { name: 'IT', score: 9.2, color: '#3182CE', reasoning: '' },
  { name: '', score: 8.8, color: '#DD6B20', reasoning: '' },
  { name: '', score: 8.7, color: '#805AD5', reasoning: '' },
  { name: '', score: 8.5, color: '#38A169', reasoning: '' },
  { name: '', score: 7.6, color: '#E53E3E', reasoning: '' },
];

// 
const sortedData = [...industryData].sort((a, b) => b.score - a.score);

// 
const sectionTitleStyle = {
  fontSize: '1.3rem',
  fontWeight: 'bold',
  textAlign: 'center',
  marginBottom: '10px',
  color: '#2c3e50',
  borderBottom: '2px solid #3182CE',
  display: 'inline-block',
  paddingBottom: '5px',
  marginTop: '10px'
};

function IndustryMatchChart() {
  return (
    <div>
      <div style={{ textAlign: 'center' }}>
        <h2 style={sectionTitleStyle}></h2>
      </div>
      <p className="section-subtitle">
        
      </p>
      <p className="section-source">
        NIRA<br />
        10
      </p>
      <div className="chart-container">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart
            layout="vertical"
            data={sortedData}
            margin={{ top: 20, right: 30, left: 120, bottom: 5 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis type="number" domain={[0, 10]} />
            <YAxis 
              dataKey="name" 
              type="category" 
              width={120} 
              tick={{ fill: '#333', fontSize: 12 }}
            />
            <Tooltip 
              formatter={(value, name, props) => {
                return [`${value}: ${props.payload.reasoning}`, ''];
              }}
              cursor={{ fill: 'rgba(0, 0, 0, 0.05)' }}
            />
            <Bar dataKey="score" name="">
              {sortedData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={entry.color} />
              ))}
            </Bar>
          </BarChart>
        </ResponsiveContainer>
      </div>
      
      <div className="research-insight">
        : <br />
        
      </div>
    </div>
  );
}

export default IndustryMatchChart;